body {
  margin: 0;
  min-width: 250px;
  font-family: Arial, sans-serif;
  background-color: #f0f0f0; /* light gray background */
}

* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  cursor: pointer;
  position: relative;
  padding: 12px 8px 12px 40px;
  list-style-type: none;
  background: #ffe6cc; /* warm beige color */
  font-size: 18px;
  transition: 0.2s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-bottom: 1px solid #ddd; /* light gray border */
}

ul li:nth-child(odd) {
  background: #fff7e6; /* light beige color */
}

ul li:hover {
  background: #ffd7be; /* warm orange color */
}

ul li.checked {
  background: #c6e2b5; /* muted green color */
  color: #333;
  text-decoration: line-through;
}

ul li.checked::before {
  content: '';
  position: absolute;
  border-color: #333;
  border-style: solid;
  border-width: 0 2px 2px 0;
  top: 10px;
  left: 16px;
  transform: rotate(45deg);
  height: 15px;
  width: 7px;
}

.close {
  position: absolute;
  right: 0;
  top: 0;
  padding: 12px 16px 12px 16px;
  background-color: #ff9900; /* vibrant orange color */
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.close:hover {
  background-color: #ff69b4; /* bright pink color */
}

.header {
  background-color: #3498db; /* bright blue color */
  padding: 30px 40px;
  color: white;
  text-align: center;
  border-bottom: 1px solid #ddd; /* light gray border */
}

.header:after {
  content: "";
  display: table;
  clear: both;
}

input {
  margin: 0;
  border: none;
  border-radius: 0;
  width: 75%;
  padding: 10px;
  float: left;
  font-size: 16px;
  background-color: #f7f7f7; /* light gray background */
}

.addBtn {
  padding: 10px;
  width: 25%;
  background-color: #2ecc71; /* bright green color */
  color: #555;
  float: left;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 0;
  border: none;
}

.addBtn:hover {
  background-color: #1abc9c; /* darker green color */
}